#profile {
    width: 100%;
}

#profile .content {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    padding: 50px;
}

#profile .content .name {
    color: black;
    font-size: 40px;
    font-weight: 700;

    margin-bottom: 50px;
}

#profile .content .info-panel {
    display: grid;
    grid-template-columns: auto auto;
    gap: 15px 75px;

    margin-bottom: 50px;
}

#profile .content .info-panel .heading {
    color: black;
    font-size: 18px;
    font-weight: 700;
}

#profile .content .info-panel .info {
    color: rgba(0, 0, 0, 0.7);
    font-size: 18px;
    font-weight: 400;
}