#footer {
    width: 100%;

    display: flex;
    flex-direction: column;

    margin-top: 50px;

    background-color: #F5F5F5;
}

@media (max-width: 800px) {
    #footer {
        margin-top: 30px;
    }
}

@media (min-width: 800px) {
    #footer .button-panel {
        width: 100%;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;

        padding: 30px 0px 30px 50px;

        background-color: white;
    }

    #footer .button-panel div {
        margin-right: 30px;
    }
}

@media (max-width: 800px) {
    #footer .button-panel {
        width: 100%;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        padding: 30px 25px;

        background-color: white;
    }
}

@media (min-width: 1300px) {
    #footer .logo-panel {
        width: 100%;

        display: flex;
        flex-direction: column;
        align-items: flex-start;

        padding: 25px 100px;
    }

    #footer .logo-panel .logo {
        width: 250px;
    }
}

@media (max-width: 1300px) {
    #footer .logo-panel {
        width: 100%;

        display: flex;
        flex-direction: column;
        align-items: flex-start;

        padding: 30px;
    }

    #footer .logo-panel .logo {
        width: 250px;
    }
}

@media (min-width: 1300px) {
    #footer .top-panel {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        margin: 50px 100px 0px 100px;
        padding-bottom: 50px;

        border-bottom: 1px solid lightgray;
    }

    #footer .top-panel .column {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    #footer .top-panel .column .title {
        color: #383838;
        font-size: 18px;
        font-weight: 700;

        margin-bottom: 10px;
    }

    #footer .top-panel .column .info {
        color: #383838;
        font-size: 18px;
        font-weight: 400;

        padding: 3px 0px;
    }
}

@media (max-width: 1300px) and (min-width: 1100px) {
    #footer .top-panel {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        margin: 50px 50px 0px 50px;
        padding-bottom: 50px;

        border-bottom: 1px solid lightgray;
    }

    #footer .top-panel .column {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    #footer .top-panel .column .title {
        color: #383838;
        font-size: 18px;
        font-weight: 700;

        margin-bottom: 10px;
    }

    #footer .top-panel .column .info {
        color: #383838;
        font-size: 18px;
        font-weight: 400;

        padding: 3px 0px;
    }
}

@media (max-width: 1100px) {
    #footer .top-panel {
        display: flex;
        flex-direction: column;

        margin: 30px 30px 0px 30px;
        padding-bottom: 30px;

        border-bottom: 1px solid lightgray;
    }

    #footer .top-panel .column {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        margin-bottom: 50px;
    }

    #footer .top-panel .column .title {
        color: #383838;
        font-size: 18px;
        font-weight: 700;

        margin-bottom: 10px;
    }

    #footer .top-panel .column .info {
        color: #383838;
        font-size: 18px;
        font-weight: 400;

        padding: 3px 0px;
    }
}

#footer .bottom-panel {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 25px;
}

#footer .bottom-panel .text {
    color: #383838;
    font-size: 15px;
    font-weight: 400;
    text-align: center;
}